import {
	mapState
} from 'vuex'

import reserveDate from "@/components/reserve-date/reserve-date.vue"
export default {
	components: {
		reserveDate
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	data() {
		return {
			priceList: []
		}
	},
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	},
	methods: {
		changeDay(data) {
			console.log(data)
		},
		toEdit() {
			uni.navigateTo({
				url: './scheduleEdit',
				animationType: 'pop-in',
				animationDuration: 200
			});
		}
	}
}
